<template>
    <van-field v-model="v_code" required label="" placeholder="4">
        <countdown v-if="counting" slot="button" :time="60000" @countdownend="countdownend" class="countdown">
            <template slot-scope="props">{{ +props.seconds || 60 }}</template>
        </countdown>
        <van-button v-else slot="button" size="small" type="primary" @click="sendCode"></van-button>
    </van-field>
</template>
<script>
import { Row, Col, Toast } from "vant";
import validateMobile from "vant/lib/utils/validate/mobile";
import {USER_SENDCODE } from "@/api/user";
export default {
  name: "mobileCode",
  props: {
      mobile:{
          type: String,
          default: ''
      },
      scene:{
          type: String,
          default: ''
      }
  },
  components: {
      validateMobile,
      Toast,
      USER_SENDCODE
  },
  data() {
    return {
        counting: false,
        v_code: '', //
        phone: this.mobile, // 
        scenes: this.scene, // 
    }
  },
  watch:{
    mobile(newValue){
      this.phone = newValue
    }
  },
  methods: {
    //
    sendCode() {
        let checkMobile = validateMobile(this.phone)
        if(!checkMobile){
            Toast('')
            return false
        }
        if (!this.counting) {
            this.$reqPost(USER_SENDCODE, {
                mobile: this.phone,
                operation: this.scenes
            }).then((res) => {
                this.codeBack(res)
            }).catch((err) => {
                this.codeBack(err)
            });
        }
    },
    //
    codeBack(res){
        if(res.data.code !=0){
            let msg = res.data.msg ? res.data.msg : "";
            Toast.fail(msg);
            return false
        } else {
            this.counting = true;
            Toast.success('');
        }
    },
    countdownend() {
        this.counting = false;
    }
  }
}
</script>

<style>
  .countdown {
    color: #4b0;
  }
</style>




